export default "/*******************************/\n/******* Page Header CSS *******/\n/*******************************/\n.page-header {\n    position: relative;\n    padding: 90px 0;\n    text-align: center;\n    background: #aa9166;\n    margin-bottom: 3rem;\n}\n.page-header h2 {\n    position: relative;\n    color: #121518;\n    font-size: 60px;\n    font-weight: 700;\n    margin-bottom: 20px;\n    padding-bottom: 5px;\n}\n.page-header h2::after {\n    position: absolute;\n    content: \"\";\n    width: 100px;\n    height: 2px;\n    left: calc(50% - 50px);\n    bottom: 0;\n    background: #121518;\n}\n.page-header a {\n    position: relative;\n    padding: 0 12px;\n    font-size: 22px;\n    color: #121518;\n}\n.page-header a:hover {\n    color: #ffffff;\n}\n.page-header a::after {\n    position: absolute;\n    content: \"/\";\n    width: 8px;\n    height: 8px;\n    top: -2px;\n    right: -7px;\n    text-align: center;\n    color: #121518;\n}\n.page-header a:last-child::after {\n    display: none;\n}\n@media (max-width: 767.98px) {\n    .page-header h2 {\n        font-size: 35px;\n    }\n    \n    .page-header a {\n        font-size: 18px;\n    }\n}\n/**********************************************/\n.rows .Label{\n    text-transform: uppercase;\n    margin-bottom: 0 !important;\n    margin-top: .4rem !important;\n    font-family: 'Catamaran', sans-serif;\n    letter-spacing: 0.1rem;\n    font-weight: 700;\n    color: #aa9166;\n}\n.btn {\n    padding: 10px 25px;\n    font-size: 18px;\n    font-weight: 500;\n    letter-spacing: 1px;\n    text-transform: uppercase;\n    color: #aa9166;\n    background: #121518;\n    border: 2px solid #aa9166;\n    border-radius: 0;\n    transition: .3s;\n}\n.btn.disabled, .btn:disabled{\n    cursor: not-allowed;\n    pointer-events: all !important;\n}\n.btn:hover {\n    color: #121518;\n    background: #aa9166;\n}\nagm-map {\n    height: 300px;\n}\n/*# sourceMappingURL=data:application/json;base64, */";